import { T_UnitConversionMap } from "types/tools/unitConverter";
import { T_FavoriteCodes } from "components/UnitConverter/types";

export const conversionFormulas = {
  baseUnitCode: "K",
  fromBaseTo: {
    C: (K: Kelvin) => K - 273.15,
    F: (K: Kelvin) => ((K - 273.15) * 9) / 5 + 32
  },
  toBaseFrom: {
    C: (C: Celcius) => C + 273.15,
    F: (F: Fahrenheit) => ((F - 32) * 5) / 9 + 273.15
  }
};

type Kelvin = number;
type Fahrenheit = number;
type Celcius = number;

